#wrap{
    width: 100vw;
    height: 100vh;
    background-color: #DC9696;
    background-image: url(../img/back2.png);
    overflow: hidden;
    position: relative;
    animation: back 60s infinite alternate linear;
}
/* | */
header{
    /* background-color: rgb(173, 73, 73); */
    height: 10vh;
}
#logo{
    position: fixed;
    top: 25px;
    left: 50px;
    width: 200px;
}
.myCart{
    position: absolute;
    right: 0;
    top: 0;
    font-size: 3rem;
}
/*  */
section{
    /* background-color: rgb(116, 25, 25); */
    display: flex;
    justify-content: center;
    height: 80vh;
    align-items: center;
    /* padding: 10vh 0; */
}
.goods{
    width: 80%;
    /* background-color: #fff; */
    display: flex;
}
.product{
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    font-weight: 600;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.1);
}
.product >*{
    width: 100%;
    margin: 10px 0;
}
.product img{
    width: 70%;  
}
.product .info{
    width: 40%;
    display: flex;
    justify-content: space-between;
    font-size: 2rem;
}
/*  */
#notice{
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgb(105, 105, 105,0.5);
}
.noticeCard{
    width: 300px;
    height: 400px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 5px 5px 6px rgb(168, 168, 168);
    text-align: center;
    line-height: 50px;
    margin: calc((100vh - 400px)/2) auto;
}
.noticeCard span{
    background-color: rgb(228, 122, 122);
    padding: 10px 30px;
    border-radius: 5px;
    font-size: 1.5rem;
    color: #fff;
}
/*  */
aside{
    width: 320px;
    height: 100%;
    background-color: #278D98;
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    flex-wrap: wrap;
}
aside>*{
    width: 90%;
    margin: 15px 5%;
    color: #fff;
}
.cartPart1{
    height: 5%;
    display: flex;
    justify-content: space-between;
    font-size: 1.5rem;
    font-weight: 700;
    /* background-color: rgb(250, 181, 181); */
}
.cartPart2{
    /* background-color: rgb(172, 55, 55); */
    height: 65%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.buyList{
    width: 99%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.buy{
    width: 90%;
    display: flex;
    align-items: center;
    /* background-color: rgb(134, 212, 97); */
}
.buyPart1{
    width: 50%;
}
.buyPart1 img{
    width: 120px;
}
.buyPart2{
    width: 40%;
    /* display: flex;
    flex-wrap: wrap; */
    line-height: 2rem;
    text-align: center;
}
/* .buyPart2>*{
    width: 100%;
} */
.quantity{
    display: flex;
    justify-content: space-evenly;
}
.buyPart3{
    width: 10%;
}
.cartPart3{
   border-top: 3px solid #fff;
   font-size: 3rem;
   font-weight: 900;
   text-align: center;
   padding-top: 20px;
   /* background-color: rgb(95, 35, 35); */
}
.cartPart3 span{
   letter-spacing: 3rem;
}
/*  */
.count{
    width: 80%;   
}
.ps{
    text-align: center;
}
.ps p{
    font-size: 12px;
    border-bottom: 1px solid white;
    padding-bottom: 5px;
}
.spend{
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
}
/*  */
.pointer{
    cursor: pointer;
}
.cartHide{
    transform: translateX(320px);
}
.hide{
    display: none;
}
/*  */
@keyframes back{
    0%{
        background-position: 0 0;
    }
    100%{
        background-position: 1000% 1000%; 
    }
}
